button, .btn{
  apperance: none;
  color: var(--color-red);
  border: 4px solid var(--color-red);
  background-color: var(--color-gold);
  padding: 10px 20px;
  font-size: 36px;
  border-radius: 36px;
  user-select: none;
}

button.sm, .btn.sm{
  padding: 5px 10px;
  font-size: 24px;
  border-width: 2px;
}



